/* 통일된 스타일 main.css */

html, body {
  font-family: 'Pretendard', 'Noto Sans', 'Segoe UI', sans-serif;
  font-size: 16px;
  margin: 0;
  padding: 0;
  background: #f9fafb;
  min-height: 100vh;
  color: var(--text-color);
}

:root {
  --primary-color: #6366f1;
  --secondary-color: #818cf8;
  --accent-color: #06b6d4;
  --bg-glass: rgba(255, 255, 255, 0.15);
  --glass-blur: 20px;
  --text-color: #111827;
  --text-light: #ffffff;
  --transition-fast: 0.3s ease;
}

[data-theme="dark"] {
  --bg-glass: rgba(0, 0, 0, 0.25);
  --text-color: #f3f4f6;
  --background-color: #1f2937;
}

/* 버튼 통일 */
button {
  font-family: inherit;
  font-size: 1rem;
  padding: 12px 24px;
  background: var(--primary-color);
  color: var(--text-light);
  border: none;
  border-radius: 16px;
  cursor: pointer;
  transition: background var(--transition-fast), transform var(--transition-fast);
  box-shadow: 0 6px 12px rgba(0,0,0,0.1);
}

.hero-image-only {
  height: calc(100vh - 80px);
  padding-top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f9fafb; /* 원하는 색상 가능 */
}

.carousel-wrapper {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 20px;
  background: white;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
}

button:hover {
  background: var(--secondary-color);
  transform: translateY(-2px);
}

/* 인풋 통일 */
input, textarea, select {
  font-family: inherit;
  font-size: 1rem;
  padding: 12px;
  border: 1px solid #d1d5db;
  border-radius: 12px;
  outline: none;
  width: 100%;
  box-sizing: border-box;
  transition: border-color var(--transition-fast), box-shadow var(--transition-fast);
  background: #ffffff;
}

input:focus, textarea:focus, select:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.3);
}

/* 통일된 NavBar 스타일 */
nav.glass-navbar {
  position: sticky;
  top: 0;
  z-index: 100;
  width: 100%;
  background: var(--bg-glass);
  backdrop-filter: blur(var(--glass-blur));
  -webkit-backdrop-filter: blur(var(--glass-blur));
  box-shadow: 0 6px 16px rgba(0,0,0,0.1);
}

.navbar-container {
  max-width: 1200px;
  margin: auto;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
}

.navbar-left, .navbar-center, .navbar-right {
  display: flex;
  align-items: center;
}

.navbar-center a, .navbar-right a {
  margin: 0 15px;
  color: var(--text-color);
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  transition: color var(--transition-fast);
}

.navbar-center a:hover, .navbar-right a:hover {
  color: var(--primary-color);
}

.logo {
  font-size: 1.75rem;
  font-weight: 800;
  text-decoration: none;
  color: var(--text-color);
}

/* 모바일 메뉴 스타일 */
.mobile-menu {
  display: none;
  position: fixed;
  top: 80px; /* navbar height만큼 내려줌 */
  left: 0;
  width: 100%;
  background: var(--bg-glass);
  backdrop-filter: blur(var(--glass-blur));
  -webkit-backdrop-filter: blur(var(--glass-blur));
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  z-index: 90;
}

.mobile-menu.show {
  display: flex;
}

.mobile-menu a, .mobile-menu button {
  margin: 10px 0;
  font-size: 1.2rem;
  color: var(--text-color);
  text-decoration: none;
  font-weight: 600;
  transition: color var(--transition-fast);
  background: none;
  border: none;
  cursor: pointer;
}

.mobile-menu a:hover, .mobile-menu button:hover {
  color: var(--primary-color);
}

/* 햄버거 메뉴 버튼 */
.hamburger {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 30px;
  height: 25px;
}

.hamburger span {
  width: 100%;
  height: 3px;
  background: var(--text-color);
  margin: 3px 0;
  border-radius: 3px;
  transition: all 0.3s ease;
}

.hamburger.open span:nth-child(1) {
  transform: translateY(6px) rotate(45deg);
}

.hamburger.open span:nth-child(2) {
  opacity: 0;
}

.hamburger.open span:nth-child(3) {
  transform: translateY(-6px) rotate(-45deg);
}

/* 모바일 화면에서 햄버거 표시 */
@media (max-width: 768px) {
  .hamburger {
    display: flex;
  }

  .desktop-menu {
    display: none;
  }
}

/* 반응형 */
@media (max-width: 768px) {
  .navbar-center, .navbar-right {
    display: none;
  }
}
